var _ = require('lodash').noConflict();
var Q = require('q');
var fpsQ = require('util/fps-q');
var preload = require('preload-js');
var easeljs = require('createjs-easeljs');
var config = require('config');

var templates = {
    canvas: require('templates/render-canvas.handlebars')
};

// ---------------------------------------------------------------------------

function QuestionRenderer() {
}

var methods = {

    _sprite: null,
    _spriteData: {},
    _stage: null,
    _el: null,

    init: function() {
        var renderCanvas = templates['canvas']({});
        document.body.insertAdjacentHTML('beforeend', renderCanvas);
        this._el = document.getElementById('mg-render-canvas');
        this._stage = new createjs.Stage(this._el);
        return this._getSpriteData();
    },

    renderQuestion: function(question, options) {
        var self = this;
        var colors = options.colors;
        var defaultColor = [0,0,0,1];
        var queue = [];

        var computeChoiceColor = function(index) {
            if(colors && colors.choices) {
              return colors.choices[index] || colors.choices[index % colors.choices.length];
            } else {
                return defaultColor;
            }
        };

        queue.push({
            img: question.questionDisplaySprite,
            options: {
                color: (colors && colors.question)
                    ? colors.question
                    : defaultColor
            }
        });

        _.each(question.questionChoicesSprites, function(choice, choiceIndex) {
            queue.push({
                img: choice,
                options: {
                    color: computeChoiceColor(choiceIndex)
                }
            });
        });

        var imgData = _.map(queue, function(qItem) {
            return self._renderSprite(qItem.img, qItem.options);
        });

        question.questionDisplay = imgData[0];
        question.questionChoices = imgData.slice(1, imgData.length);

        return question;
    },

    renderQuestions: function(questions, options) {
        var self = this;
        return _.map(questions, function(question) {
            return self.renderQuestion(question, options);
        });
    },

    _renderSprite: function(sprite, options) {

        var self = this;
        var maxBounds = {
            x: 0,
            y: 0
        };

        var calculateBounds = function(val) {
            return {
                x: Math.max((val.x + val.w), maxBounds.x),
                y: Math.max((val.y + val.y), maxBounds.y)
            };
        };

        _.each(sprite, function(val, index) {
            var rCharacter = self._frames[val.i].clone();
            rCharacter.x = val.x;
            rCharacter.y = val.y;
            rCharacter.scaleX = val.w / self._frames[val.i].sourceRect.width;
            rCharacter.scaleY = val.h / self._frames[val.i].sourceRect.height;
            self._stage.addChild(rCharacter);
            self._stage.update();
            maxBounds = calculateBounds(val);
        });

        var bounds = this._stage.getBounds();
        var maskColor = [0, 0, 0, 1];
        var optionsColor = options.color;

        this._stage.filters = [
          new createjs.ColorFilter(
              maskColor[0],
              maskColor[1],
              maskColor[2],
              maskColor[3],
              optionsColor[0],
              optionsColor[1],
              optionsColor[2],
              optionsColor[3]
          )
        ];

        this._stage.cache(0, 0, bounds.width, bounds.height);
        var imgData = this._stage.getCacheDataURL();
        this._clearRenderStage();

        return imgData;
    },

    _clearRenderStage: function() {
        this._stage.uncache();
        this._stage.removeAllChildren();
        this._stage.update();
    },

    _getSpriteData: function() {
        var self = this;
        var deferred = Q.defer();
        var xhr = new XMLHttpRequest();
        var spriteUrl = config.apiRoot + '/game/getSpriteSheet?sprite_key=DEFAULT';
        var queue = new createjs.LoadQueue(true, '', true);
        xhr.open('GET', spriteUrl);
        xhr.onload = function() {
            var response = xhr.status === 200
                ? JSON.parse(xhr.response)
                : null;
            if(response && !response.error) {
                queue.loadFile({id:'sprite', src: config.spriteImage});
                queue.on('complete', function() {
                    self._sprite = queue.getResult('sprite');
                    var frames = _.map(response.sprite_map, function(val, index) {
                        var crop = new createjs.Bitmap(self._sprite);
                        crop.sourceRect = new createjs.Rectangle(val.x, val.y, val.w, val.h);
                        return crop;
                    });
                    self._frames = frames;
                    deferred.resolve(response);
                });
            } else {
                deferred.reject(response);
            }
        };
        xhr.send();
        return deferred.promise;
    }
  };

_.extend(QuestionRenderer.prototype, methods);

module.exports = QuestionRenderer;



